import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Stepper from "@mui/material/Stepper";

const dispatchedEvents = [];

const SrsMuiStepper = function (props, ref) {
  return (
    <Stepper
      activeStep={props.activeStep}
      alternativeLabel={props.alternativeLabel}
      orientation={props.orientation}
      nonLinear={props.nonLinear}
      connector={props.connector}
      theme={props.muiTheme}
    >
      <slot name="default" />
    </Stepper>
  );
};

const styles = `
  /* Custom styles for the MuiStepper component */
`;

const props = [
  {
    name: "activeStep",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "alternativeLabel",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "orientation",
    defaultValue: "horizontal",
    type: "string",
  },
  {
    name: "nonLinear",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "connector",
    defaultValue: null,
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Stepper if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-step"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiStepper),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
